import Login from './Login.jsx';
import AddUserForm from './AddUserForm.jsx';
import ChangePasswordForm from './ChangePasswordForm.jsx';
import { useState, useEffect } from 'react';

export default function App() {
  const [tab, setTab] = useState('home');
  const [user, setUser] = useState(null);

  useEffect(() => {
  const stored = localStorage.getItem('currentUser');
  if (stored) {
    try {
      const parsed = JSON.parse(stored);
      if (parsed && parsed.name) {
        setUser(parsed);
      } else {
        console.warn(' Invalid user object:', parsed);
        localStorage.removeItem('currentUser');
      }
    } catch (e) {
      console.error(' Invalid JSON in currentUser:', e);
      localStorage.removeItem('currentUser');
    }
  }
}, []);



  function handleLogout() {
    localStorage.removeItem('currentUser');
    setUser(null);
  }

  if (!user) {
    return <Login onLogin={setUser} />;
  }

  return (
    <div className="app">
      <nav className="navbar">
        <div className="navbar-left">
          <h1> ERP Dashboard</h1>
        </div>
        <div className="navbar-right">
          <div className="dropdown">
            <button className="dropdown-toggle"> {user.name}</button>
            <div className="dropdown-menu">
              {user.role === 'admin' && (
                <button onClick={() => setTab('add-user')}>  </button>
              )}
              <button onClick={() => setTab('change-password')}>   </button>
              <button onClick={handleLogout}> </button>
            </div>
          </div>
        </div>
      </nav>
      <div className="layout">
        <aside className="sidebar">
          <button onClick={() => setTab('home')}> </button>
          {user.role === 'admin' && (
            <button onClick={() => setTab('add-user')}> </button>
          )}
          <button onClick={() => setTab('change-password')}>  </button>
        </aside>
        <main className="main">
          {tab === 'home' && <h2>   , {user.name}</h2>}
          {tab === 'add-user' && <AddUserForm currentUser={user} />}
          {tab === 'change-password' && <ChangePasswordForm currentUser={user} />}
        </main>
      </div>
    </div>
  );
}
